@use 'index' as *;

$moduleBG: rgba(69, 94, 129, 0.704);

@mixin statsContainer() {
    @include flex-container(center, space-between);
    flex-direction: column;
    background-color: $moduleBG;
    color: white;
    margin: 10px;
    border: 1px solid white;
}

.calcMainContainer {
    @include flex-container(center, space-between);
    flex-direction: column;
    width: 300px;
    height: 700px;
    background-color: rgba(180, 204, 237, 0.082);
    color: white;
    margin: 10px;
    border: 0.5px solid white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    input[type=number] {
        appearance: textfield;
        -moz-appearance: textfield;
        font-family: $font-main;
        margin: 0;
        border: 1px solid black;
        color: white;
        background-color: rgba(32, 32, 41, 0.323);
    }

    input[type=number]:focus {
        box-shadow: none;
        margin: 0;
        outline: 1px solid rgb(149, 149, 205);
    }

    input[type=number]::selection {
        background-color: white;
        color: black;
    }

    .charStatsContainer {
        @include statsContainer;
        width: 100%;
        height: 50%;
    }

    .enemyStatsContainer {
        @include statsContainer;
        width: 100%;
        height: 30%;
    }

    .statSetsContainer {
        @include statsContainer;
        width: 100%;
        height: 30%;
    }

    .resultsContainer {
        @include statsContainer;
        width: 100%;
        height: 20%;
    }
}