@use 'index' as *;

.mainContainer {
    @include flex-container;
    flex-direction: column;
    width: 100%;
    gap: 5%;

    .atkSection {
        @include flex-container;
        flex-direction: column;
        width: 90%;
        border: 1px solid white;
        border-radius: 5px;
    }

    .dmgSrcSection {
        @include flex-container(center, start);
        width: 90%;
        margin: 5%;
        gap: 1em;
    }
}